备等功能的管理系统，目的是拓展客户，促使成交。绑定了微信公众号，主要运行在移动端。")]),_c('p',{staticClass:"project__text"},[_vm._v("第一个版本是由我的经理也是公司的全栈开发的，主要用的是jQuery和mui，没有使用构建工具，没有模块化，没有组件化，没有前端工程化的概念。随着需求和功能的增加，代码量逐渐庞大，这种技术架构的劣势就出现了：新增功能效率慢，各个功能之间的逻辑关系错综复杂，页面的样式和结构看的让人想吐，可扩展性差，维护成本高，已临近崩溃。")]),_c('p',{staticClass:"project__text"},[_vm._v("系统有录入客户>报备客户>跟进>辩客>到访>接待>客户成交！这一套流程，每个都是独立的功能模块却又层层嵌套，我用了一周时间理解透彻将要开发的产品，剖析各个模块之间的对应关系。然后我要求进行重构，担任前端兼设计，主导整个项目的前端，独立开发，也是可怜只有我一个前端，使用的是vue技术栈，完美的解决了上面的问题。我让项目快速上线，给业务人员使用，让他们提出问题，然后迭代开发，再扩展后续功能。")]),_c('p',{staticClass:"project__text"},[_vm._v("开发期间遇到几个大问题，第一个是和后端沟通不够，大家各自闷头开发，一副没事别打扰的工作状态，导致双方接口联调总是出问题，影响前端进度，还有实现功能的想法不一致，导致已经写好的代码都白费了。所以我提议每天早上开发之前，大家用5-10分钟说一说，总结昨天的开发内容，今天将要开发的内容，有什么问题，哪里需要协作。这样既保证了我们的沟通，总监也明确的知道当前的开发进度，更好的掌控整开发期间遇到几个大问题，第一个是和后端沟通不够，大家各自闷头开发，一副没事别打扰的工作状态，导致双方接口联调总是出问题，影响前端进度，还有实现功能的想法不一致，导致已经写好的代码都白费了。所以我提议每天早上开发之前，大家用5个项目的走向。")]),_c('p',{staticClass:"project__text"},[_vm._v("第二个是BUG。使用vue到目前为止遇到的最大BUG，白屏问题，页面上什么都没有。（后来我才知道错怪vue了）。总之这是一个让人瑟瑟发抖的BUG，而且不是一个问题造成的，让我先是痛苦不堪，解决之后欣喜若狂。")]),_c('p',{staticClass:"project__text"},[_vm._v("刚开始是IOS10以下和部分老版本安卓手机出现问题，我使用移动端调试，根据错误信息进行查找，感谢Google，让我在webpack-dev-server的GitHub上找到了答案，是因为2.7.1以后的版本不再兼容低版本浏览器，降级就可以解决。")]),_c('p',{staticClass:"project__text"},[_vm._v("部分手机在微信浏览器中出现白屏（这次和手机的版本无关），有时会，有时不会，在其他非微信浏览器中却都可以正常访问。我尝试加polyfill做代码兼容，却并没有用处。针对这手机开启本地调试却没有问题了，发布到线上服务器测试又有问题，发布到测试服务器测试却又没有问题，十分诡异。后来我终于知道是微信缓存问题，清除缓存都没用，只有退出微信账号，重新登录，才能解决问题。")]),_c('p',{staticClass:"project__text"},[_vm._v("公司有一百多位分销商，为了满足他们的使用需求，我又添加了组织架构、图表统计、抢客、房源、销控、合同、对账单等，这是一个非常大的系统，已经稳定的运行了半年，想了解更多可以去这里 https://github.com/licong96。")])])]),_c('div',{staticClass:"project"},[_c('h4',{staticClass:"project__name"},[_vm._v("项目：小程序版的管理系统")]),_c('div',{staticClass:"project__content"},[_c('p',{staticClass:"project__text"},[_vm._v("web移动端的系统运行稳定之后，为了满足案场人员和其他团队的使用需求，要开发一个小程序版本。功能已经熟悉了，只是在系统原有的基础上增加了一些模块，这里面会用到一些组件，比如根据字母排列的城市选择组件、列表左右滑动组件、组织架构多级联动组件，我没有去找第三方插件，而是使用小程序组件化开发方案自己手写，这样可以更好的控制代码体积，更好的做优化，也更好的锻炼自己。")])])])]),_c('div',{staticClass:"company"}