等，可惜产品参差不齐，现在都死的差不多，这些早期项目都是用来学知识的。")]),_c('p',{staticClass:"project__text"},[_vm._v("学到了不少知识，我也发现了很多问题，比如：同样的代码在不同的手机中显示不一样，有时需要写更多的代码来解决一些问题。APICloud自称能媲美原生APP，其实有很大差距，对于配置较低的手机，项目小看不出来，项目大就会有响应缓慢，甚至卡顿现象，体验不流畅，为了解决这个问题，首先考虑的是自己代码问题，学了一些代码优化，确实有效。平台也有不完善的地方，但是以后应该会更好。")])])])])])]),_c('section',{staticClass:"chunk"},[_c('h2',{staticClass:"chunk__title"},[_vm._v("开源项目和作品")]),_c('div',{staticClass:"chunk__content"},[_c('div',{staticClass:"chunk__block"},[_c('h3',{staticClass:"chunk__title-sub"},[_vm._v("香菇街")]),_c('div',{staticClass:"chunk__content"},[_c('div',{staticClass:"chunk__text"},[_vm._v("学习vue的时候开发的女装商城，纯前端项目，用jsonp抓取了蘑菇街的线上数据，来渲染页面，微信上打开更好，地址：\n            "),_c('a',{staticClass:"chunk__link",attrs:{"href":"https://github.com/licong96/women-wear-mall","target":"_blank"}},[_vm._v("https://github.com/licong96/women-wear-mall")])])])]),_c('div',{staticClass:"chunk__block"},[_c('h3',{staticClass:"chunk__title-sub"},[_vm._v("城市选择器插件")]),_c('div',{staticClass:"chunk__content"},[_c('div',{staticClass:"chunk__text"},[_vm._v("省-市-区三级联动城市选择器插件，地址：\n            "),_c('a',{staticClass:"chunk__link",attrs:{"href":"https://github.com/licong96/lc-city-selector","target":"_blank"}},[_vm._v("https://github.com/licong96/lc-city-selector")])])])])])])])}]
